.container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 90vh;
}

.form, .select, .input, .button {
    font-family: "Open Sans";
    font-style: normal;
    padding: 10px;
    margin: 10px;
    height: fit-content;
    color: rgb(93, 93, 93);
    text-align: justify;
    border: 0.5px solid gray;
    border-radius: 15px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
} 
.form{
    width: fit-content;
    min-width: 600px;
    padding: 40px;
}
.label{
    width: fit-content;
}
.input{
    width: 50px;
}

.bottom{
    margin: 20px 0 0 0 ;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 50;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(128, 128, 128, 0.365);
}

.message{
    max-width: 500px;
    padding: 20px;
    background: white;
    border: 0.5px solid gray;
    border-radius: 15px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    line-height: 20px;

}

@media (max-width: 1000px){
    .form, .select, .input, .button {
        padding: 5px;
        margin: 5px;
        height: fit-content;
        color: rgb(93, 93, 93);
        text-align: justify;
        border: 0.5px solid gray;
        border-radius: 10px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    } 
    
    .form{
        margin: 30px auto;
        width: fit-content;
        min-width: fit-content;
        padding: 20px;
    }
    .input{
        width: 50px;
    }
    
    .bottom{
        margin: 20px 0 0 0 ;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .modal{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 50;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(128, 128, 128, 0.365);
    }
    
    .message{
        max-width: 500px;
        padding: 20px;
        background: white;
        border: 0.5px solid gray;
        border-radius: 15px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        line-height: 20px;
    
    }   
}
