.timeItemList {
    margin-left: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.timeItem {
    margin: 0 3px 5px 3px;
    width: 73px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #d9e8ff;
    border-radius: 11px;
    font-size: 16px;
    cursor: default;
    transition: 0.1s;
}
.timeItem:hover {
    background: #6ba6ff;
}
.moreItem {
    cursor: pointer;
}
